.footer {
  padding-top: 25px;
  padding-bottom: 55px;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) {
    margin-right: 32px;
  }

  &__link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: var(--light-color);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--accent-color);
    }
  }
}

.social {
  &__icons {
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) {
    margin-right: 10px;
  }

  &__link svg path {
    fill: var(--light-color);
    transition: fill 0.3s ease-in;
  }

  &__link--tg:hover svg path {
    fill: #039BE5;
  }

  &__link--vk:hover svg path {
    fill: #0077FF;
  }
}
