.hero {
  padding-top: 50px;

  &__container {
    display: flex;
    align-items: center;
  }

  &__content {
    margin-top: 50px;
    width: 50%;
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
    font-family: "Gilroy", sans-serif;
    font-weight: 500;
    font-size: 46px;
    line-height: 105%;
    color: var(--light-color);

    span {
      color: var(--accent-color);
    }
  }

  &__descr {
    margin-bottom: 18px;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.03em;
    color: var(--light-color);
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 16px 61px;
    min-width: 240px;
    font-family: 'Gilroy', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);
    border-radius: 14px;
    background-color: var(--accent-color);
    transition: background-color 0.3s ease-in;

    &:hover {
      background-color: var(--accent-darkColor);
    }

    &:active {
      background-color: var(--accent-color);
    }
  }

  &__requirements {
    width: 50%;

    &-title {
      margin: 0;
      padding: 0;
      max-width: 280px;
      margin-right: auto;
      margin-bottom: 37px;
      margin-left: auto;
      font-weight: 400;
      font-size: 22px;
      line-height: 120%;
      text-align: center;
      color: var(--light-color);
    }

    &-cards {
      display: grid;
      justify-content: center;
      grid-template-columns: repeat(2, 134px);
      row-gap: 20px;
      column-gap: 32px;
    }

    &-card {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 24px 10px 20px 10px;
      border: 3px solid #7668CA;
      border-radius: 15px;
    }

    &-img {
      width: 40px;
      height: 40px;
      margin-bottom: 12px;

      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    &-img--fluid {
      width: 52px;
      height: 40px;
      margin-bottom: 12px;

      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      text-align: center;
      color: var(--light-color);
    }
  }
}
